.Form_wrapper {
  /* border: 1px solid brown; */
  /* flex: 0 0 50%; */
  width: calc((100%-80px) / 2);
  background: linear-gradient(138.68deg, #dce6ff 8.7%, #ffffff 123.75%);
  border-radius: 5px;
  text-align: center;
  padding: 25px 60px 40px;
  margin-top: 30px;
}

.Input {
  font-family: 'Lato';
  width: 100%;
  font-size: 20px;
  line-height: 24px;
  border: none;
  color: var(--color-darkblue);
  background: #ffffff;
  padding: 15px 35px;
  box-shadow: inset 1px 1px 1px rgba(92, 129, 225, 0.32);
  border-radius: 5px;
}

.Input:focus {
  outline: 1px solid burlywood;
}
.Input::placeholder {
  font-size: 20px;
  line-height: 24px;
  color: #bcbcbc;
}

.Password_icon {
  position: absolute;
  color: #bcbcbc;
  top: 12px;
  right: 15px;
}

.User_Box {
  position: relative;
  margin-bottom: 20px;
}
