.Checkbox_login {
  margin-bottom: 20px;
  position: relative;
}

.Checkbox_text {
  font-family: 'Lato';
  font-size: 14px;
  line-height: 17px;

  color: var(--color-darkblue);
}

.Form_Error {
  font-size: 0.875rem;
  color: #ff0000;
}

.Forgot_pass {
  font-family: inherit;
  font-weight: 700;
  font-size: 14px;
  line-height: 137%;
  text-decoration-line: underline;
  color: var(--color-darkblue);
  margin-top: 32px;
  margin-bottom: 35px;
}
.Create_acc {
  line-height: 137%;
  color: var(--color-darkblue);
}
.Create_desr {
  font-weight: 700;
  font-size: 15px;
  line-height: 137%;

  text-decoration-line: underline;
  background-color: transparent;
  border: 0;
  color: var(--color-darkblue);
  cursor: pointer;
}
